import React from 'react'
import styled from 'styled-components'
import { hex2rgb } from '../../Utils'
import ColorPicker from './ColorPicker';


class ThemePickerForm extends React.Component{

    constructor(props){
        super(props);
        this.saveTheme = this.saveTheme.bind(this);
        this.state = {
            colors: {}
        }

        this.setColor = this.setColor.bind(this);
        this.saveTheme = this.saveTheme.bind(this);
    }

    saveTheme(event){
        event.preventDefault()
        this.props.handleSave(this.state.colors)
    }

    setColor(key, color){
        this.setState({colors: {...this.state.colors, [key]: color}});
    }
    render(){
        return(
            <StyledForm onSubmit={this.saveTheme}>
                {Object.keys(this.props.theme.colors).map(function(color, index){
                    return <ColorPicker 
                                label={`${this.props.theme.colorLabels[color]}: `} 
                                color={this.props.theme.colors[color].toRgb()}
                                onChangeComplete={this.setColor}
                                onChange={this.setColor}
                                key={index}
                                colorName={color}
                                />
                }, this)}
                <input type="submit" value="Save" />
            </StyledForm>
        )
      }
  }

  /**
   * <ColorPicker label="Primary: " color={{r: 20, g: 20, b: 100, a: 1}}/>
                <ColorPicker label="Primary: " color={{r: 20, g: 20, b: 100, a: 1}}/>
   */

  const StyledForm = styled.form`
    
  `

  export default ThemePickerForm